@import '../../global.scss';
.section {

    @include mobile {
        display: none;
    }

    @include desktop {

        width: 100%;
        display: flex;
        justify-content: space-between;
        height: 300px;

        .left, .center, .right {

            width: 30%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            border-radius: 30px;
            height: 100%;
            background-color: white;


            .top {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 50px;
                background-color: red;
                border-top-left-radius: 30px;
                border-top-right-radius: 30px;

                h3 {
                    color: white;
                    font-weight: 700;
                }
            }

            .center {
                width: 170px;
                height: 170px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    
                }
            }

            .bottom {
                width: 100%;
                height: 50px;
                display: flex;
                justify-content: center;

                p {
                    width: 80%;
                    
                    height: 40px;
                    font-size: 14px;
                    text-justify:distribute;
                    text-align:center;
                    color: white;
                    font-weight: 500;
                    background-color: #23C116;
                    border-radius: 50px;
                }

            }


        }

    }
}