@import '../../global.scss';

.slider {

    @include desktop {

        
        width: 100%;
        display: none;
        flex-direction: column;

        .top {
            width: 100%;
            height: 60px;
        }
        

        .center {
            border-radius: 30px;

            height: 100%;
            img {
                width: 100%;
                height: 100%;
                object-fit: fill;
                
            }
        }

        .bottom {
            width: 100%;
            height: 60px;
        }
    }

    @include mobile {

        display: flex;
        flex-direction: column;

        width: 95%;
        max-width: 515px;
        background-color: #E74C4C;
        height: 20em;
        border-radius: 20px;
        margin: 30px 0;
        justify-content: space-between;

        .top {
            width: 100%;
            height: 60px;
            



        }

        .center {
            width: 100%;
            height: calc(100% - 120px);
            background-color: white;
            overflow: hidden;

            img {
                width: 100%;
                height: 20em;
                object-fit:scale-down;
                
            }
        }


        .bottom {
            display: flex;
            height: 60px;
            align-items: center;
            justify-content: center;

            .container {
                width: 100%;
                display: flex;
                //align-items: center;
                justify-content: center;
                //border: 1px solid black;
                .dot{
                    cursor: pointer;
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    background-color: white;
                    margin: 0 10px;
                    
                }
            }

            

        }
    }
}