@import '../../global.scss';

.contact {

    @include desktop {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        width: 100%;
        height: 10em;
        background-color: white;
        margin-top: 30px;
        border-top-left-radius: 30px;
        border-top-right-radius: 30px;
        font-size: 23px;

        .container {
            width: 20%;
            display: flex;
            align-items: center;
            justify-content: space-around;

            svg {
                font-size: 40px;
            }
        }
    }
    @include mobile {

        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 95%;
        height: 10em;
        background-color: white;
        border-top-left-radius: 20px;
        border-top-right-radius: 20px;
        margin-top: 30px;

        h3{
            font-size: 26px;
        }
        svg {
            margin: 0 10px;
            font-size: 35px;
        }
    }
}