@import '../../global.scss';

.intro {

    //height: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80%;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include desktop {
            width: 100%;
            object-fit: cover;
            position: relative;

            h3 {
                position: absolute;
                right: 30px;
                top: 100px;
                color: white;
                width: 250px;
                font-size: 40px;

            }


            

            img {
                border-radius: 30px;
                margin: 30px 0;
                width: 100%;
                
            }
        }

        img {
            width: 100%;
        }
        @include mobile {

            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            border-bottom-right-radius: 20px;
            border-bottom-left-radius: 20px;
            background-color: #E74C4C;
            width: 95%;
            
            
            min-width: 360px;
            

            img {
                width: 100%;
                //position: absolute;
            }

            h3 {
                color: white;
                font-weight: 700;
                text-align: center;
                font-size: 26px;
            }

        }
    
    }
    

   


}