@import '../../global.scss';
.topbar {

    width: 850px;
    display: flex;
    height: 47px;
    color: white;
    border-bottom: 1px solid white;
    transition: all 1s ease;
    

    .left, .right {

        width: 50%;
    
    }

    .left {
        display: flex;
        align-items: center;
        justify-content: start;
    }

    .right {
        display: flex;
        align-items: center;
        justify-content: end;
        font-size: 14px;

        @include desktop {
            svg {
                display: none;
            }

            ul {
                li {
                    a {
                        text-decoration: none;
                        color: white;
                    }
                }
            }
        }
        

        
        ul {
            display: flex;
            li {
                list-style: none;
                margin-right: 10px;
                font-weight: 700;
                cursor: pointer;
                a {
                    list-style: none;
                    text-decoration: none;
                    transition: all 1s ease;

                    &:visited {
                        color: white;
                    }
                    
                }
                

            }

            li:last-child {
                margin-right: 0;
            }

            li:not(:first-child)::before {
                content: '|';
                color: white;
                margin-right: 5px;

            }
        }
    }
    
    @media (max-width: "767px") {
        width: 100vw;
        color: white;
        display: flex;
        background-color: #29E251;

        .left, .right {

            width: 50%;
            display: flex;
            
        
        }

        .left {
            display: flex;
            align-items: center;

            h2 {
                margin-left: 20px;
            }


        }

        .right {
            align-items: center;
            justify-content: end;

            .list {
                display: none;

            }
            h4 {
                display: none;
            }
            svg {
                
                font-size: 40px;
                margin-right: 20px;
                cursor: pointer;
                z-index: 2;
            }
        }
        
    }
    



    


}

