@import '../../global.scss';

.locations {
    
    @include desktop {
        width: 100%;
        position: relative;



        .location {
            width: 100%;
            height: 25em;
            margin-top: 30px;
            border-radius: 30px;
    
            background-color: white;
            z-index: 1000;
    


        }


    }

    @include mobile {
        width: 95%;
        height: 30em;
        margin-top: 30px;
        

        .location {
            width: 100%;
            height: 100%;
            border-radius: 30px;
        }
    }


}