@import '../../global.scss';

.plans {

    @include desktop {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        height: 30em;
        //overflow: hidden;
        border-radius: 30px;
        

        .center{
            
            background-color: #F6F6F6;
            display: flex;
            align-items: center;
            justify-content: center;
            height: calc(100% - 120px);
            width: 100%;

            .left {
                
                width: 50%;
                height: 100%;
                //display: flex;

                .plans {
                    
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;

                    .plan-1, .plan-2, .plan-3 {
                        
                        width: 100%;
                        height: 100%;
                        position: relative;
                        color: white;

                        h3, h4 {
                            position: absolute;
                        }

                        h3 {
                            top: 0.2em;
                            left: 6em;
                            
                        }

                        .plan-price {
                            top: .9em;
                            font-size: 2em;
                            right: 1.5em
                        }

                        .plan-speed {
                            top: 1.4em;
                            font-size: 1.5em;
                            right: 8em

                        }

                        img {
                            width: 100%;
                        }
                        svg {
                            color: white;
                            position: absolute;
                            left: .65em;
                            top: .5em;
                            font-size: 3em;
                        }
                    }
                    

                }





                


            }

            .right {

                width: 50%;
                img {
                    width: 100%;
                    object-fit:cover;
                    
                }

                
            }


        }



        .top {
            display: flex;
            align-items: center;
            justify-content: center;
            border-top-left-radius: 30px;
            border-top-right-radius: 30px;
            width: 100%;
            height: 60px;
            background-color: #E74C4C;
            color: white;


        }

        .bottom {
            width: 100%;
            height: 60px;
            background-color: #E74C4C;
            border-bottom-right-radius: 30px;
            border-bottom-left-radius: 30px;
        }

       


    }
    @include mobile {
        width: 95%;
        display: flex;
        flex-direction: column;
        
        height: 25em;

        .top{
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            width: 100%;
            height: 60px;
            background-color: #E74C4C;
            border-top-left-radius: 20px;
            border-top-right-radius: 20px;
        }

        .center {
            height: calc(100% - 120px);
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            img {
                width: 100%;
                height: auto;
                object-fit: fill;
            }



            .girl {
                display: none;
            }
        }


        .bottom {
            width: 100%;
            height: 60px;
            background-color: #E74C4C;
            border-bottom-left-radius: 20px;
            border-bottom-right-radius: 20px;

        }
        
    }
}