@mixin mobile {
    @media (max-width: #{767px}) {
        @content;
        
    }
}


@mixin desktop {

    @media (min-width: #{768px}) {
        @content;
    }
    
}

body {
    background-color: #F07070;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to bottom, #F07070, rgb(242, 118, 118), rgb(240, 189, 189));

    transition: all 1s ease;
}

html  {
    scroll-behavior: smooth;
}

