@import '../../global.scss';

.cover {


    @include desktop {
        width: 100%;
        height: 10em;
        background-color: white;
        margin: 30px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-radius: 30px;


        .container {

            display: flex;
            height: 100%;
            width: 90%;
            justify-content: space-between;
            align-items: center;

            .first, .second, .third, .fourth {
                display: flex;
                height: 50%;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;
                margin: 0 10px;
                font-size: 12px;
            }

           

            svg {
                font-size: 40px;
            }
        }
    }

    @include mobile {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 361px;
        
        border-radius: 20px;
        height: 15em;
        background-color: white;
        margin-bottom: 30px;

        .container {
            height: 80%;
            width: 80%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;

            .first, .second, .third, .fourth {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;

            }



            svg {
                font-size: 30px;
                margin-bottom: 10px;
            }

            h3 {
                font-size: 12px;
            }



        }
    }
}