@import '../../global.scss';

.menu {

    @include mobile {

        width: 50%;
        height: 100vh;
        position: absolute;
        right: -50%;
        background-color: #29E251;
        transition: all 1s ease;
        align-items: center;
        justify-content: center;
        z-index: 0;
        display: flex;
        overflow: hidden;

        .container {
            display: flex;
            width: 100%;
            height: 20%;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            font-size: 23px;



            a {
                text-decoration: none;
                color: white;
                font-weight: 700;


            }
        }



        &.active {
            display: flex;
            position: fixed;
            right: 0;
            align-items: center;
            justify-content: center;
            z-index: 0;

            .container {
                display: flex;
                width: 100%;
                height: 20%;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;
                font-size: 23px;



                a {
                    text-decoration: none;
                    color: white;
                    font-weight: 700;


                }
            }
        }


        
    }

}